import React, { Component } from "react";
import {
  WhiteSpace,
  Card,
  WingBlank,
  InputItem,
  Button,
  Toast,
  Modal,
  List,
  Switch
} from "antd-mobile";
import data from "./data";
import api from "../../api/index";

// 通过自定义 moneyKeyboardWrapProps 修复虚拟键盘滚动穿透问题
// https://github.com/ant-design/ant-design-mobile/issues/307
// https://github.com/ant-design/ant-design-mobile/issues/163
const isIPhone = new RegExp("\\biPhone\\b|\\biPod\\b", "i").test(
  window.navigator.userAgent
);
let moneyKeyboardWrapProps;
if (isIPhone) {
  moneyKeyboardWrapProps = {
    onTouchStart: e => e.preventDefault()
  };
}
const alert = Modal.alert;

const compare = property => {
  return function(obj1, obj2) {
    var value1 = obj1[property];
    var value2 = obj2[property];
    return value1 - value2;
  };
};

const clone = obj => {
  return JSON.parse(JSON.stringify(obj));
};

const MAP_LABEL = {
  checked: "是否发布",
  index: "排序",
  name: "名称",
  spec: "规格",
  price: "价格",
  unit: "单位",
  url: "图片地址"
};

class Home extends Component {
  state = {
    goodsList: data.sort(compare("index"))
  };
  handleInputOnChange = (value, id, label) => {
    const { goodsList } = this.state;
    const list = clone(goodsList);
    const key = this.handleFindKeyByMAP_LABEL(label);
    list.forEach(e => {
      if (e.id === id) {
        e[key] = value;
      }
    });
    this.setState({ goodsList: list });
  };
  handleFindKeyByMAP_LABEL = val => {
    return Object.keys(MAP_LABEL).filter(e => MAP_LABEL[e] === val)[0];
  };
  handleSave = () => {
    Toast.success("Load success !!!", 1);
  };
  handleDel = () => {
    alert("删除", "删除将不可恢复！", [
      { text: "取消", onPress: () => console.log("取消") },
      { text: "确定", onPress: () => console.log("确定") }
    ]);
  };
  handleAdd = () => {
    const { goodsList } = this.state;
    const list = clone(goodsList);
    const index = list.length + 1;
    list.push({
      index,
      name: "红糖夹沙肉",
      price: 27,
      unit: "袋",
      spec: "500g",
      id: "a0f306dcly1fxd71ht5q6j20jn0eqwgv.jpg",
      src: "http://wx3.sinaimg.cn/mw690/a0f306dcly1fxd71ht5q6j20jn0eqwgv.jpg",
      checked: true
    });
    this.setState({ goodsList: list });
    api.addGoods({
      success: data => {
        console.log(data);
      },
      param: {
        index,
      name: "红糖夹沙肉",
      price: 27,
      unit: "袋",
      spec: "500g",
      id: "a0f306dcly1fxd71ht5q6j20jn0eqwgv.jpg",
      src: "http://wx3.sinaimg.cn/mw690/a0f306dcly1fxd71ht5q6j20jn0eqwgv.jpg",
      checked: true
      }
    });
  };
  handleWitchChange = () => {};
  render() {
    const { goodsList } = this.state;
    return (
      <WingBlank size="lg">
        {goodsList.map(e => {
          const { name, url, unit, price, index, id, spec, src, checked } = e;
          const inputInfo = [
            {
              placeholder: "不能超过" + goodsList.length,
              label: MAP_LABEL.index,
              value: index
            },
            {
              placeholder: "请输入" + MAP_LABEL.name,
              label: MAP_LABEL.name,
              value: name
            },
            {
              placeholder: "请输入" + MAP_LABEL.spec,
              label: MAP_LABEL.spec,
              value: spec
            },
            {
              placeholder: "请输入" + MAP_LABEL.price,
              label: MAP_LABEL.price,
              value: price
            },
            {
              placeholder: "请输入" + MAP_LABEL.unit,
              label: MAP_LABEL.unit,
              value: unit
            },
            {
              placeholder: "请输入" + MAP_LABEL.url,
              label: MAP_LABEL.url,
              value: src
            }
          ];
          return (
            <div key={id} style={{ width: "100%" }}>
              <Card>
                <Card.Header title={name} thumb={url} />
                <Card.Body>
                  {/* <img src={src} alt="图片地址不正确" style={{width:"100%"}}/> */}
                  <List.Item
                    extra={
                      <Switch
                        checked={checked}
                        onChange={e =>
                          this.handleInputOnChange(e, id, MAP_LABEL.checked)
                        }
                      />
                    }
                  >
                    {MAP_LABEL.checked}
                  </List.Item>
                  {inputInfo.map((ee, ii) => {
                    const { placeholder, label, value } = ee;
                    return (
                      <InputItem
                        key={ii.toString()}
                        type={"input"}
                        onChange={e => this.handleInputOnChange(e, id, label)}
                        value={value}
                        placeholder={placeholder}
                        clear
                        moneyKeyboardAlign="right"
                        moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                      >
                        {label}
                      </InputItem>
                    );
                  })}
                  <Button type="warning" onClick={e => this.handleDel(id)}>
                    删除
                  </Button>
                  <WhiteSpace size="lg" />
                  <Button type="primary" onClick={e => this.handleSave(id)}>
                    保存
                  </Button>
                </Card.Body>
              </Card>
              <WhiteSpace size="lg" />
            </div>
          );
        })}
        <WhiteSpace size="lg" />
        <Button onClick={this.handleAdd}>新增</Button>
        <WhiteSpace size="lg" />
      </WingBlank>
    );
  }
}

export default Home;
